import React, { useState } from "react";
import Row from "./Row";

export default function Table(props) {
  const [Countries, SetSountries] = useState(props.Countries);
  const [search, setSearch] = React.useState("");

  const handleSearch = (event) => {
    setSearch(event.target.value);
  };

  let count = Countries.length;

  const data = Countries.filter((item) =>
    item.Country.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div>
      <label htmlFor="search">
        <strong>Search by Country : </strong>
        <input
          id="search"
          type="text"
          onChange={handleSearch}
          placeholder={`Search ${count} countries...`}
        />
      </label>
      <h1>Total Active Cases: {props.Active}</h1>
      {data.length === 0 && <h3>No Country Exist</h3>}
      {data.length !== 0 && (
        <table>
          <thead>
            <tr>
              <th>Country</th>
              <th>Active Cases</th>
            </tr>
          </thead>

          <tbody>
            {data.map((country, index) => {
              return (
                <Row
                  key={index}
                  country={country.Country}
                  cases={
                    country.TotalConfirmed -
                    country.TotalDeaths -
                    country.TotalRecovered
                  }
                />
              );
            })}
          </tbody>
        </table>
      )}
    </div>
  );
}
